@import "../../brand.scss";
@import "../../variables.scss";

.lotto-number-option {
	border: none;
	outline: none;
	background: white;
	border-radius: 50%;
	width: 35px;
	height: 35px;
	text-align: center;
	font-weight: 600;
	font-size: 1.1em;
	text-decoration: underline;
	-ms-flex-order: 100;
	    order: 100;
	&--filled {
		background: -webkit-radial-gradient(10px 10px, farthest-corner, red, darkred);
		background: -o-radial-gradient(10px 10px, farthest-corner, red, darkred);
		background: radial-gradient(farthest-corner at 10px 10px, red, darkred);
		-webkit-box-shadow: 0 3px 6px rgba(0,0,0,0.16);
		        box-shadow: 0 3px 6px rgba(0,0,0,0.16);
		color: white;
		
	}
	@media (min-width: $iPhone8) {
		width: 40px;
		height: 40px;
		font-size: 1.2em;
	}
}


