.divider {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	width: 100%;
	max-width: 420px;
	&__line {
		-ms-flex-positive: 1;
		    flex-grow: 1;
		background: black;
		opacity: 0.25;
		height: 1px;
	}
	&__title {
		margin: 0 4px;
		font-weight: 700;
		text-transform: uppercase;
		-webkit-user-select: none;
		   -moz-user-select: none;
		    -ms-user-select: none;
		        user-select: none;
	}
}


